@import url(https://fonts.googleapis.com/css2?family=Lato:ital@0;1&display=swap);
body {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
  height: 100%;
  color: rgb(26, 130, 221);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Lato", "Segoe UI", "Castoro",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bottombar {
  color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 7vh;
  background-color: rgb(255, 234, 250);
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icons {
  position: absolute;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.bottombar a {
  color: rgb(5, 122, 255);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.bottombar a:hover {
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  box-shadow: 0px 0px 20px -5px violet;
}

.toTheLeft {
  margin-left: 0.2vw;
}

img {
  margin-left: 1vw;
  margin-right: 1vw;
  max-width: 60px;
  max-height: 60px;
}

.attributions {
  align-self: flex-end;
  font-size: 0.7rem;
}

.container-fluid {
  justify-content: center;
  align-items: center;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: linear-gradient(180deg, rgb(73, 62, 126), rgb(14, 0, 54));
  background-attachment: fixed;
  background-position: center;
}

.vertical {
  border: groove 10px;
  border-image-source: radial-gradient(
    circle,
    rgb(0, 0, 0),
    rgba(46, 27, 61, 0.534)
  );
  border-image-slice: 1;
  border-left: none;
  border-right: none;
  border-top: none;
  background-color: rgba(2, 0, 7, 0.459);
  min-height: 150px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding-left: 7vw;
  padding-right: 7vw;
  box-shadow: 0px 10px 100px -10px rgb(26, 26, 26);
  text-align: center;
  justify-content: center;
}

h3 {
  width: 100%;
  font-size: 10vw;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.836);
  -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 0.15rem;
  -webkit-text-stroke-color: rgb(0, 0, 0);
  background: linear-gradient(180deg, rgb(0, 255, 157) 0%, rgb(234, 0, 255));
  -webkit-background-clip: text;
          background-clip: text;
}

@media only screen and (max-width: 768px) {
  h3 {
    font-size: 74px;
  }
}

.topbar {
  top: 0;
  position: absolute;
  height: 7vh;
  width: 100%;
  background-color: rgba(7, 2, 14, 0.863);
  box-shadow: 0px 15px 50px -10px rgb(0, 0, 0);
}

.nametitle {
  -webkit-animation-duration: 1500ms;
          animation-duration: 1500ms;
}

h5 {
  font-weight: 700;
  font-style: italic;
  color: rgb(0, 0, 0);
  text-shadow: 0px 0px 7px rgba(255, 255, 255, 0.753);
  background-color: rgba(196, 178, 201, 0.534);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding-left: 1vw;
  padding-right: 1vw;
  margin-left: auto;
  margin-right: auto;
}

.no-gutters,
.row {
  margin-right: 0;
  margin-left: 0;
  padding-right: 0;
  padding-left: 0;
}

.titleName {
  top: 20%;
  position: relative;
  min-width: 400px;
  display: flex;
  justify-content: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-top: 15vh;
  padding-bottom: 15vh;
  background-color: rgba(215, 226, 235, 0.11);
  box-shadow: 0px 25px 100px -10px rgb(26, 26, 26),
    inset 0px 0px 75px -20px rgba(241, 210, 255, 0.644);
  -webkit-animation-name: titleIntro, fadeIn;
          animation-name: titleIntro, fadeIn;
  -webkit-animation-duration: 1500ms;
          animation-duration: 1500ms;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
}

.title {
  border-top: groove 3vh rgb(24, 20, 43);
  box-shadow: inset 0px 30px 50px -30px rgb(44, 44, 44);
  display: flex;
  justify-content: center;
  margin: auto;
  height: 100vh;
  text-align: center;
}

.mainContent {
  display: flex;
  text-align: center;
  height: 93vh;
  margin-bottom: 2vh;
}

.evenodd:nth-child(odd) {
  background-color: rgba(0, 217, 255, 0.26);
}

.aboutMe {
  height: 70vh;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(223, 194, 247, 0.342);
  box-shadow: 0px 15px 50px -10px rgb(0, 0, 0),
    inset 0px 0px 150px -30px rgba(241, 210, 255, 0.521);
  backdrop-filter: blur(5.5px);
  -webkit-backdrop-filter: blur(5.5px);
  -webkit-animation: fadeInUp;
          animation: fadeInUp;
  -webkit-animation-duration: 1200ms;
          animation-duration: 1200ms;
}

.aboutMeTitle {
  margin-top: 3vh;
  -webkit-animation: fadeInUp;
          animation: fadeInUp;
  -webkit-animation-duration: 1400ms;
          animation-duration: 1400ms;
}

@-webkit-keyframes titleIntro {
  0% {
    transform: translateY(50%);
  }
}

@keyframes titleIntro {
  0% {
    transform: translateY(50%);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

